<template>
  <div class="start">
    <h1 class="title-sentence ma-5">{{ sentence }}</h1>
    <v-container grid-list text-xs-center>
      <v-layout row wrap justify-center>
        <v-flex xs12 sm6>
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-text-field
              v-model="name"
              :rules="rules.noSpaceOnly"
              label="Nom"
            ></v-text-field>

            <v-text-field
              v-model="firstName"
              :rules="rules.noSpaceOnly"
              label="Prénom"
            ></v-text-field>

            <v-text-field
              v-model="email"
              :rules="rules.emailSyntax"
              label="E-mail"
            ></v-text-field>

            <!--   <v-text-field
              v-model="password"
              :append-icon="show1 ? 'visibility' : 'visibility_off'"
              :rules="[rules.required, rules.min]"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
              label="Normal with hint text"
              hint="At least 8 characters"
              counter
              @click:append="show1 = !show1"
            ></v-text-field>

            <v-text-field
              v-model="confirmPassword"
              :append-icon="show2 ? 'visibility' : 'visibility_off'"
              :rules="[rules.required, rules.min]"
              :type="show2? 'text' : 'password'"
              name="input-10-2"
              label="Normal with hint text"
              hint="At least 8 characters"
              counter
              @click:append="show2 = !show2"
            ></v-text-field> -->

            <v-checkbox
              v-model="checkbox"
              color="primary"
              :rules="[v => !!v || 'You must agree to continue!']"
              label="Vous acceptez les Conditions d’utilisation, la Politique de confidentialité et la Politique relative aux cookies de SpeakingKong."
              required
            ></v-checkbox>

            <v-btn color="primary" @click="submit" :loading="loading">
              S'inscrire
            </v-btn>
          </v-form>
          <div class="ma-4 already-account">
            <div>Vous avez déjà un compte?</div>
            <div>
              <router-link to="/login" tag="span">Se connecter</router-link>
            </div>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
