<script>
import * as libFb from "@/fb";

export default {
  data() {
    return {
      loading: false,
      sentence: "",
      hookSentences: [
        "Partagez du temps entre natifs",
        "Révolutionnons l’apprentissage des langues",
        "Parlez immédiatement et sans RDV"
      ],
      name: "",
      firstName: "",
      email: "",
      password: "",
      confirmPasswd: "",
      checkbox: false,
      valid: false,

      /* ************** */
      show1: false,
      show2: true,
      show3: false,
      show4: false,
      rules: {
        required: value => !!value || "Champs obligatoire",
        min: v => v.length >= 8 || "Min 8 characters",
        noSpaceOnly: [
          v => !!v || this.rules.required(),
          v => (v.trim().length === 0 ? "Invalid" : true)
        ],
        emailSyntax: [
          v => !!v || this.rules.required(),
          v => {
            // eslint-disable-next-line
            const regexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
            return regexp.test(v) ? true : "Email non valide";
          }
        ]
      }
    };
  },
  created() {
    this.sentence = this.hookSentences[Math.floor(Math.random() * 3)];
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        // eslint-disable-next-line
        console.log(this.name + this.firstName + this.email);
        this.loading = true;
        var currentDate = new Date();
        const user = {
          name: this.name,
          firstName: this.firstName,
          inscription: currentDate,
          email: this.email,
          password: "test12345"
        };

        libFb.auth
          .createUserWithEmailAndPassword(this.email, user.password)
          .then(cred => {
            // eslint-disable-next-line
            console.log(cred);
            this.loading = false;
            this.$emit("userAuth");
            this.$router.push("/board");
          });

        /* libFb.db
          .collection("users")
          .add(user)
          .then(() => {
            this.loading = false;
            this.$emit("userAuth");
            this.$router.push("/board");
          }); */
      }
    }
  }
};
</script>

