<template>
  <v-container fill-height>
    <audio id="songCall" loop>
      <source src="../assets/songCall.mp3" type="audio/mp3" />
    </audio>
    <audio id="songCloseCall">
      <source src="../assets/songCloseCall.mp3" type="audio/mp3" />
    </audio>

    <v-layout column justify-center align-center>
      <div
        v-if="btnState === 'call-initial'"
        class="main-container"
        v-on:click="
          btnState = 'call-sending';
          sendingCall();
        "
      >
        <div class="circle-gradient">
          <div class="circle-gradient-content">
            <v-icon id="call-phone-initial">phone</v-icon>
          </div>
        </div>
        <!-- <div class="container-1-phone"></div>
        <div class="container-2-phone"></div>
        <div class="container-3-phone"></div>
        <div class="container-4-phone"></div> -->
        <!-- <div class="container-5-phone">
          <v-icon id="call-phone-initial">phone</v-icon>
        </div> -->
      </div>

      <div
        class="call-action"
        v-if="btnState === 'call-sending'"
        v-on:click="stopSendingCall()"
      >
        <!-- <div id="circle-expand"> -->
        <v-progress-circular
          :rotate="360"
          :size="160"
          :width="5"
          :value="100"
          color="#fff"
          style="background-color: #fff; border-radius: 50%"
        >
          <v-progress-circular
            :rotate="-90"
            :size="150"
            :width="3"
            indeterminate
            color="#64DD17"
          >
            <v-icon id="call-phone-pending">phone</v-icon>
          </v-progress-circular>
        </v-progress-circular>
        <!-- </div> -->
        <div id="test1">
          <span class="test2"></span>
          <span class="test2"></span>
          <span class="test2"></span>
        </div>
      </div>

      <div v-if="btnState === 'call-hangup'" v-on:click="finishCall()">
        <v-btn id="close-call">
          <v-icon id="icon-phone-ending">phone</v-icon>
        </v-btn>
      </div>
    </v-layout>
  </v-container>
</template>


<script>
export default {
  name: "CallButton",
  data() {
    return {
      btnState: "call-initial",
      setTimeout,
      audioElement: new Audio(),
      audioElementClose: new Audio()
    };
  },
  mounted() {
    this.audioElement = document.getElementById("songCall");
    this.audioElementClose = document.getElementById("songCloseCall");
  },
  methods: {
    sendingCall() {
      this.audioElement.currentTime = 0;
      this.audioElement.volume = 0.05;
      this.audioElement.play();
      this.setTimeout = setTimeout(() => {
        this.btnState = "call-hangup";
        this.audioElement.pause();
      }, 9000);
    },
    stopSendingCall() {
      this.audioElement.pause();
      clearTimeout(this.setTimeout);
      this.btnState = "call-initial";
    },
    finishCall() {
      this.audioElement.pause();
      this.audioElementClose.currentTime = 0;
      this.audioElementClose.volume = 0.05;
      this.audioElementClose.play();
      setTimeout(() => {
        this.btnState = "call-initial";
      }, 600);
    }
  }
};
</script>

